.view {
    text-align: right;
    table-layout: fixed;
}

.headers {
    color: white;
    background-color: skyblue;
    line-height: 2.4rem;
    font-size: 1.2rem;
    text-align: center;
}

.table {
    border-spacing: 0;
    border: .025rem solid teal;
    border-collapse: collapse;
    table-layout: auto;
    width: 100%;
}

th, td {
    border: .025rem solid teal;
    padding: .25rem;
}

.items {
    line-height: 2rem;
    font-size: 1.2rem;
    text-align: center;
}

.footers {
    color: white;
    background-color: skyblue;
    line-height: 2rem;
    font-size: 1.2rem;
    text-align: center;
}

.button {
    color: white;
    background: teal;
    padding: .375rem .75rem;
    border: 1px solid teal;
    border-radius: .25rem;
    margin: .5rem;
    font-size: 1rem;
    line-height: 1.5;
    text-align: center;
}

.input {
    background-color: transparent;
    height: 100%;
    width: 75%;
    border: 2px solid rgba(0, 0, 0, 0.23);
    border-radius: 7px;
    margin-bottom: .25rem;
}

.input:hover {
    border: 2px solid black;
}

.input:focus-visible  {
    outline: none!important;
    border: 2px solid #1976d2;
}
